@import "../helpers/variables";

.input-component-light {
  background-color: $lightItemBackground;
  &:focus {
    border: 2px solid black;
  }
}

.input-component-dark {
  background-color: $darkItem;
  &:focus {
    border: 2px solid white;
  }
}


#input-component {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  height: $listItemHeight;
  width: calc(50% + 20px);
  margin-bottom: 20px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  border-radius: 5px;
  overflow: hidden;
  padding: 10px 0;
  outline: 0 !important;

  @media (max-width: 700px) {
    width: 90%;
  }

  #outer-circle {
    display: flex;
    background-image: linear-gradient(hsl(192, 100%, 67%), hsl(280, 87%, 65%));
    height: $circleDiameter;
    width: $circleDiameter;
    border-radius: 50%;
    z-index: 5;
    margin-left: 10px;

    #circle {
      justify-content: center;
      align-items: center;
      height: calc($circleDiameter - 1px);
      width: calc($circleDiameter - 1px);
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      z-index: 6;
      pointer-events: none;
    }
  }

  .input-dark {
    background-color: $darkItem;
    color: $darkItemText1;
  }

  .input-light {
    background-color: $lightItemBackground;
    color: $lightActiveText;
  }

  .send-light {
    background-color: $lightItemBackground;
    color: $lightActiveText;
  }

  .send-dark {
    background-color: $darkItem;
    color: $darkItemText1;
  }

  #send-button {
    height: $listItemHeight;
    font-size: 1rem;
    //outline: none;
    //border: none;
    margin-right: 1rem;
    padding: 0 16px;
    font-weight: bold;
    z-index: 1;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 2px 2px;
    @media (max-width: 700px) {
      font-size: 14px;
    }
  }

  #input {
    height: $listItemHeight;
    width: 90%;
    font-size: 1rem;
    outline: none;
    border: none;
    margin-left: 1em;
    font-weight: bold;
    z-index: 1;

    @media (max-width: 700px) {
      font-size: 14px;
    }
  }
}
